<template>
<div class="subpage--main">
  <div class="handler-group">
    <Button type="primary" class="btn" shape="circle" size="small" @click="save">
      <Icon type="archive"/> 
    </Button>
    <Button type="success" class="btn" shape="circle" size="small" @click="start">
      <Icon type="power"/> 
    </Button>
  </div>
  <div ref="flow" id="flow"></div>
  <Modal
    v-model="modifyModal.isShown"
    :title="modifyModal.data.type === 'SPECIAL' ? '' : ''"
    width="360"
    @on-ok="patchKettleItem"
  >
    <Input
      v-if="modifyModal.data.type!=='SPECIAL'"
      v-model="modifyModal.data.name"
    />
    <Form v-else :model="modifyModal.data" :label-width="80">
      <FormItem label="" prop="schedulerType">
        <Select v-model="modifyModal.data.schedulerType">
          <Option v-for="vo in modifyModal.TYPE_LIST" :key="vo.key" :value="vo.key">{{vo.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="repeat">
        <Select v-model="modifyModal.data.repeat">
          <Option value="Y"></Option>
          <Option value="N"></Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="time" v-if="parseInt(modifyModal.data.schedulerType) !== 0">
        <TimePicker :value="time" format="HH:mm" placeholder="Select time" style="width:100%;" @on-change="changeTime"></TimePicker>
      </FormItem>
      <FormItem v-if="parseInt(modifyModal.data.schedulerType)===4" label="" prop="weekDay">
        <Input v-model="modifyModal.data.DayOfMonth"/>
      </FormItem>
      <FormItem v-if="parseInt(modifyModal.data.schedulerType)===3" label="" prop="DayOfMonth">
        <Input v-model="modifyModal.data.weekDay"/>
      </FormItem>
    </Form>
  </Modal>
</div>
</template>

<script>
import G6 from '@antv/g6'

G6.track(false)

const TRANSLATE_MAP = {
  x: 'xloc',
  y: 'yloc',
  type: 'type',
  name: 'name',
  hour: 'hour',
  minutes: 'minutes',
  repeat: 'repeat',
  weekDay: 'weekDay',
  DayOfMonth: 'DayOfMonth',
  schedulerType: 'schedulerType'
}

const NODES_MAP = {
  xloc: 'x',
  yloc: 'y'
}

const COLOR_MAP = {
  0: '#CED0D3',
  1: '#3C6281',
  2: '#11B513'
}

export default {
  props: {
    kettleConfig: Object,
  },
  data() {
    return {
      rawData: {},
      flowData: {
        nodes: [],
        edges: []
      },
      currentNode: null,
      graph: null,
      modifyModal: {
        isShown: false,
        currentNodeId: null,
        TYPE_LIST: [
          {
            key: '0',
            name: ''
          },
          {
            key: '2',
            name: ''
          },
          {
            key: '3',
            name: ''
          },
          {
            key: '4',
            name: ''
          }
        ],
        data: {}
      }
    };
  },
  computed: {
    time() {
      return this.modifyModal.data.hour + ':' + this.modifyModal.data.minutes
    },
    fId() {
      return this.$route.query.fId
    }
  },
  watch: {
    fId() {
      this.loadFlow()
    }
  },
  created() {
    this.loadFlow()
  },
  methods: {
    async loadFlow() {
      try {
        this.rawData = await this.$api.kettle.getKettleDetail.send({
          fId: this.fId
        })
        this.flowData = this.transformData(this.rawData)
        if(this.graph) {
          this.graph.read(this.flowData)
          return
        }
        G6.registerNode(
          'kettleItem', {
            getHtml(data) {
              const {
                model: {
                  type,
                  name
                }
              } = data
              return `<div class="item"><i class="icon ${type}"></i>${name}</div>`
            }
          },
          'html'
        )
        const graph = this.graph = new G6.Graph({
          id: 'flow',
          fitView: 'cc',
        })
        this.graph.read(this.flowData)
        this.graph.on('node:click', ({ item }) => {
          this.currentNode = item
          let model = item.getModel()
          Object.assign(this.modifyModal, {
            isShown: true,
            currentNodeId: model.id,
            data: Object.assign({}, model)
          })
        })
        var node = void 0;
        var dx = void 0;
        var dy = void 0;
        graph.on('node:dragstart', function(ev) {
          var item = ev.item;

          var model = item.getModel();
          node = item;
          dx = model.x - ev.x;
          dy = model.y - ev.y;
        });
        graph.on('node:drag', function(ev) {
          node && graph.update(node, {
            x: ev.x + dx,
            y: ev.y + dy
          });
        });
        graph.on('node:dragend', function(ev) {
          node = undefined;
        });
      } catch (e) {
        this.$Message.error(e.message)
      } finally {
      }
    },
    transformData({
      entries,
      hops
    }) {
      return {
        nodes: entries.map(vo => {
          const obj = {
            id: vo.name,
            shape: 'kettleItem',
            color: '#fff',
            style: {
              stroke: 'transparent',
              fill: 'transparent',
            },
          };
          for (let key in vo) {
            const _key = NODES_MAP[key] || key
            obj[_key] = ['x', 'y'].indexOf(_key) > -1 ? parseFloat(vo[key]) : vo[key]
          }
          return obj
        }),
        edges: hops.map((vo, index) => ({
          id: `edge${index + 1}`,
          source: vo.from,
          target: vo.to,
          color: COLOR_MAP[vo.enabled === 'N' ? 0 : 2],
          size: 2,
          style: {
            arrow: true
          }
        }))
      }
    },
    recoverData() {
      const {
        nodes,
        edges
      } = this.flowData
      return JSON.stringify({
        hops: this.rawData.hops.map((vo, index) => {
          return Object.assign(vo, {
            from: edges[index].source,
            to: edges[index].target
          });
        }),
        entries: nodes.map((vo, index) => {
          const currentNode = {};
          for (let key in TRANSLATE_MAP) {
            currentNode[TRANSLATE_MAP[key]] = vo[key];
          }
          return currentNode;
        })
      });
    },
    async start() {
      try {
        await this.$api.kettle.executeJob.send({
          fId: this.fId
        })
        this.$Message.success('')
      } catch (e) {
        this.$Modal.error({
          title: '',
          content: e.message
        })
      }
    },
    async save() {
      try {
        await this.$api.kettle.updateFile.send({
          fId: this.fId,
          content: this.recoverData()
        })
        this.$Message.success('')
      } catch (e) {
        this.$Modal.error({
          title: '',
          content: e.message
        })
      }
    },
    patchKettleItem() {
      let currentNodeId = this.modifyModal.currentNodeId
      let newNodeId = this.modifyModal.data.name
      let findNode = this.flowData.nodes.find(vo => vo.id === currentNodeId)
      if (findNode) {
        this.modifyModal.data.id = newNodeId
        Object.assign(findNode, this.modifyModal.data)
        this.flowData.edges = this.flowData.edges.map(vo => {
          if (vo.target === currentNodeId) {
            vo.target = newNodeId
          }
          if (vo.source === currentNodeId) {
            vo.source = newNodeId
          }
          return vo
        })
        this.graph.read(this.flowData)
      }
    },
    changeTime(val) {
      const data = this.modifyModal.data;
      [data.hour, data.minutes] = val.split(':')
    },
  },
};
</script>

<style lang="scss" scoped>
.handler-group {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  padding: 5px 0;
  width: 100%;
  height: 36px;

  .btn {
    float: right;
    margin: 0 5px 0 0;
    cursor: pointer;
  }
}

#flow {
  width: 100%;
  height: 100%;
}

/deep/ .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: content-box;
  max-width: 100px;
  text-align: center;
  line-height: 18px;
  font-size: 12px;
  user-select: none;
  cursor: pointer;

  .icon {
    display: block;
    overflow: visible;
    box-sizing: content-box;
    margin: 0 auto 6px;
    width: 44px;
    height: 44px;
    background-image: url("~@/assets/images/kettle.png");
  }

  .SPECIAL {
    background-position: 0 0;
  }

  .TRANS {
    background-position: 0 -44px;
  }

  .SET_VARIABLES {
    background-position: 0 -88px;
  }

  .MYSQL_BULK_LOAD {
    background-position: 0 -132px;
  }

  .SHELL {
    background-position: 0 -176px;
  }

  .SUCCESS {
    background-position: 0 -220px;
  }
}
</style>
